<template>
  <v-footer height="300px">
      <v-layout row wrap="" justify-center="">
         <v-toolbar flat>
            <v-toolbar-items flat>
        <v-layout row>
            <v-flex v-bind:key=item.genre v-for="item in toolbar">
                <v-btn flat block @click="changeroute(item.url)" slot="activator"><b>{{ item.genre }}</b></v-btn>
            </v-flex>
        </v-layout>
            </v-toolbar-items>
          </v-toolbar>
          <v-flex xs12 text-xs-center><br>: 109 : 886-2-28235699  : 886-2-28270646<br></v-flex>
      </v-layout>
  </v-footer>
</template>

<script>
export default {
 data () {
    return {
      toolbar: [
        {genre:"",url:"introduction"},
        {genre:"",url:"new"},
        {genre:"",url:"FRL"},
        {genre:"",url:"construction"},
        {genre:"",url:"lastest"},
        {genre:"",url:"instruction"},
        {genre:"",url:"equipment"},
        {genre:"",url:"relation"},
        {genre:"",url:"brands"},
        {genre:"",url:"information"}
      ]
    }
  },
   methods: {
    changeroute (url) {
      this.$router.push('/home/'+ url)
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    }
  }
}
</script>

<style>
</style>



